import React, { Component } from 'react';
import { Input, Select, Button, Modal, Icon } from 'antd';
import * as ApiProvider from '../API/ApiProvider';
import PowerSettingCell from './powersettingCell'
const { confirm } = Modal;

class Powersetting extends Component {
    constructor(props) {
        super();

        this.addUserdata = this.addUserdata.bind(this)
        this.adduserhandleChange = this.adduserhandleChange.bind(this)
        this.queryUserdata = this.queryUserdata.bind(this)
        this.getAll = this.getAll.bind(this)
    }

    state = {
        MouseClick: 0,
        name: '',
        role: 0,
        adduser: undefined,
        userList: [],
        // power: ['僅可瀏覽病房總覽、歷史資料查詢', '可瀏覽和編輯病房總覽、歷史資料查詢', '可瀏覽和編輯權限設定、系統摘要'],
        // powerName: ['唯讀使用者', '一般使用者', '系統管理者'],
        visible: false,
        radioValue: 0,
        pwdRadio: true,
        inputUsername: '',
        inputPassword: '',
        inputName: '',
        inputDoctorid: '',
        powerLogin: 0,
        EditMode: false
    }

    componentDidMount() {

        ApiProvider.getAlluser()
            .then(data => {
                this.setState({ userList: data.data })
            })
            .catch(err => { })

        const powerLogins = sessionStorage.getItem('role')
        this.setState({ powerLogin: powerLogins })
    }

    radioOnChange = e => {
        this.setState({
            radioValue: e.target.value,
        }, () => {
            if (this.state.radioValue === 1) {
                this.setState({
                    pwdRadio: true,
                })
            } else if (this.state.radioValue === 0) {
                this.setState({
                    pwdRadio: false,
                })
            }
        })
    };

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };



    queryUserdata() {
        const { userList, role } = this.state
        let fixData = userList

        if (role != 0) {
            fixData = fixData.filter(data => data.role == (role - 1))
        }

        if (this.state.name !== '') {
            fixData = fixData.filter(data => data.name.match(this.state.name) != null)
        }
        return fixData
    }

    addUserdata() {//sso 只剩下4種角色 其他先傳空直
        const name = document.getElementById("name").value
        const username = document.getElementById("username").value
        ApiProvider.register(name, '1234', this.state.adduser, username, username)
            .then(data => { this.getAll() })
            .catch(err => { })
        this.setState({ visible: false, });
    }


    getAll() {
        ApiProvider.getAlluser()
            .then(data => {
                this.setState({ userList: data.data, inputUsername: this.state.inputName, inputPassword: '', inputName: this.state.inputName, inputDoctorid: this.state.inputName })
            })
            .catch(err => { })
    }

    onChangeCallBack = (info, string) => {
        console.log(info, string)
        if (string === "delete") {
            ApiProvider.deleteUser(info.doctorid, info.role, info.name)
                .then(data => this.getAll())
                .catch(err => console.log(err))
        }
        if (string === "save") {
            ApiProvider.register(info.name, '1234', info.role, info.username, info.doctorid)
                .then(data => { this.getAll() })
                .catch(err => { })
        }
    }


    EditModule() {
        const { Option } = Select;
        return (
            <Modal
                title="新增帳號"
                visible={this.state.visible}
                onOk={this.addUserdata}
                onCancel={this.handleCancel}
                width="570px">
                <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>

                </div>
                <div style={{ display: "flex", justifyContent: 'center', alignItems: "center", marginTop: "10px" }}>
                    <div style={{ marginLeft: "10px" }}>
                        醫師帳號：
                <Input id="username" placeholder="輸入帳號" style={{ width: '120px' }} />
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        姓名：
                <Input id="name" placeholder="輸入姓名" style={{ width: '120px' }} />
                    </div>
                </div>
                <div style={{ display: "flex", justifyContent: 'center', alignItems: "center", marginTop: "10px", marginLeft: "13px" }}>
                    <div style={{ marginLeft: "0px" }}>
                        角色：{this.state.pwdRadio ?
                            <Select defaultValue={0} style={{ width: 150 }} onChange={this.adduserhandleChange.bind(this)}>
                                <Option value={0}>唯讀使用者</Option>
                                <Option value={1}>一般使用者</Option>
                            </Select> :
                            <Select placeholder='選擇角色' style={{ width: 150 }} onChange={this.adduserhandleChange.bind(this)}>
                                <Option value={2}>系統設定管理員</Option>
                            </Select>}
                    </div>
                </div>
            </Modal>
        );
    }


    handleChange = (value) => {
        this.setState({ role: value })
    }

    adduserhandleChange(value) {
        this.setState({ adduser: value })
    }


    Searchbar = () => {
        const { Option } = Select;

        return (
            <div style={{ display: 'flex', alignItems: 'center' }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div style={{ fontSize: '16px' }}>
                        角色:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Select defaultValue={this.state.role} style={{ width: 150 }} onChange={this.handleChange}>
                            <Option value={0}>全部使用者</Option>
                            <Option value={1}>唯讀使用者</Option>
                            <Option value={2}>一般使用者</Option>
                        </Select>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        姓名:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Input placeholder="輸入個案姓名" onChange={this.nameChange}></Input>
                    </div>
                </div>
            </div>
        );
    }



    render() {

        const columns = [
            {
                title: '姓名',
                dataIndex: 'name',
                key: 'name',
                width: "10%"
            },
            {
                title: '醫師帳號',
                dataIndex: 'account',
                key: 'account',
                width: "15%"
            },
            {
                title: '角色',
                dataIndex: 'role',
                key: 'role',
                width: "10%"
            },
            {
                title: '權限',
                dataIndex: 'power',
                key: 'power',
                width: "60%"
            }
        ];
        const titleCell = columns.map(function (info, index, arr) {
            return (
                <div style={{ display: "flex", alignItems: "center", padding: "0px 5px" }}>{info.title}</div>
            );
        })
        const fixData = this.queryUserdata()
        console.log(fixData)
        const cellMap = fixData.map((info, index, arr) => {
            return (
                <PowerSettingCell key={index} indexNum={index} cellData={info} callback={this.onChangeCallBack}></PowerSettingCell>
            );
        })


        return (
            <div>
                <div style={{ display: "flex", justifyContent: 'space-between', alignItems: 'center' }}>
                    {this.Searchbar()}
                    <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                        <Button style={{ width: "100%" }} onClick={this.showModal}>新增帳號</Button>
                        {this.EditModule()}
                    </div>
                </div>
                <div style={{ marginTop: '20px' }}>
                    <div style={{ display: "grid", gridTemplateColumns: "2fr 3fr 2fr 6fr 1fr", height: "50px", backgroundColor: "rgba(0,0,0,.05)" }}>
                        {titleCell}
                    </div>
                    <div style={{ height: "calc(100vh - 270px)", overflow: "auto" }}>
                        {cellMap}
                    </div>
                </div>
            </div>
        );
    }
}

export default Powersetting;
